import loading from "~/components/loading.vue";
export default {
  components: { loading },
  // middleware: ["isAuthenticated", "isAdmin"],
  data() {
    return {
      loading: true,
      total: 0,
      current: 1,
      perPage: 21,
      rangeBefore: 2,
      rangeAfter: 1,
      order: "is-centered",
      size: "is-medium",
      isSimple: true,
      isRounded: true,
      prevIcon: "chevron-left",
      nextIcon: "chevron-right",

      isCardModalActive: false,
      Word: {},
      data: [],
      Allword: [],
    };
  },
  methods: {
    getpendingWords() {
      this.data = this.Allword.filter((word) => word.status == "pending");
      this.total = this.data.length;
    },
    getapprovedWords() {
      this.data = this.Allword.filter((word) => word.status == "approved");
      this.total = this.data.length;
    },
    paginations(start, end) {
      return (this.data = this.data.slice(start, end));
    },

  },
  async created() {
    await this.$axios.$get("/api/Word").then((data) => {
      this.Allword = data;
      this.loading = false;
    });
  },

};
